import React, { Fragment } from 'react';
import Cropper from 'react-easy-crop';
import { getOrientation } from '../../../node_modules/get-orientation/browser';
import getCroppedImg from './cropImage';
import { getRotatedImage } from './rotateImage';
import './styles.css';
import Modal from '@material-ui/core/Modal';
import AddAPhotoOutlinedIcon from '@material-ui/icons/AddAPhotoOutlined';

const ORIENTATION_TO_ANGLE = {
  3: 180,
  6: 90,
  8: -90,
};

class App extends React.Component {
  state = {
    imageSrc: null,
    crop: { x: 0, y: 0 },
    zoom: 1,
    aspect: 1 / 1,
    croppedAreaPixels: null,
    croppedImage: null,
    isCropping: false,
    croppedImageShow:null
    
  }


  componentWillMount() {
    const { photo, changProfile } = this.props;
    if (changProfile) {
      this.setState({
        tookInput: false, imageSrc: photo, changProfilePic: true, cropped: false, mainImg:photo
      });
    } else {
      // console.log(photo);
      this.setState({ tookInput: false, imageSrc: photo, changProfilePic: false ,mainImg:photo});
    }
  }

  componentWillReceiveProps(np) {
    const { photo, changProfile } = np;
    if (changProfile) {
      this.setState({
        tookInput: false, imageSrc: photo, changProfilePic: true, cropped: false, mainImg:photo
      });
    } else {
      this.setState({ tookInput: false, imageSrc: photo, changProfilePic: false ,mainImg:photo});
    }
  }

  onCropChange = (crop) => {
    this.setState({ crop });
  }

  onCropComplete = (croppedArea, croppedAreaPixels) => {
    this.setState({
      croppedAreaPixels,
    });
    // console.log("done")
    this.showResult()
  }

  onZoomChange = (zoom) => {
    this.setState({ zoom });
  }

  showResult = async (pr) => {
    try {
      const croppedImage = await getCroppedImg(
        this.state.imageSrc,
        this.state.croppedAreaPixels,
        this.state.imgproperties
      );
      
      this.setState({
        croppedImage,
        isCropping: false,
      
      });
    } catch (e) {
      this.setState({  
        isCropping: false,
      });
    }
  }

  onClose = async () => {
    this.setState({
      croppedImage: null,
    });
  }

  onFileChange = async (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      let imageDataUrl = await readFile(file);

      // apply rotation if needed
      const orientation = await getOrientation(file);
      const rotation = ORIENTATION_TO_ANGLE[orientation];
      if (rotation) {
        imageDataUrl = await getRotatedImage(imageDataUrl, rotation);
      }

      this.setState({
        tookInput: true,
        imageSrc: imageDataUrl,
        crop: { x: 0, y: 0 },
        zoom: 1,
        imgproperties: file 
      });
      this.setState({ });
    }
  }

  render() {
    return (
      <div>
        {
          !this.state.changProfilePic
            ? (
              <div className="profile-img-div mt4 mb2 center">
                <div className="center">

                  {
                    (this.state.mainImg)

                      ? <img src={this.state.mainImg} id="a" className="profile-img" alt="" /> : <img src="https://cdn-images-1.medium.com/max/1200/1*PkrwrLwaq68CaqLPn7jBIw.png" className="profile-img" alt="" />
                  }
                </div>
              </div>
            )
            : (

              <div className="">
                <div className="profile-img-div mt4 mb2 center">
                  <div className="center">
                    <input
                      type="file"
                      id="avatar"
                      name="avatar"
                      accept="image/png, image/jpeg"
                      className="img-input"
                      onChange={this.onFileChange}
                    />
                    {
                       this.state.mainImg ? <img src={this.state.mainImg} id="a" className="profile-img" alt="" />
                         : <AddAPhotoOutlinedIcon className="profile-img" />
                     }
                  </div>
                </div>
                <Modal
                  open={this.state.tookInput}
                  aria-labelledby="simple-modal-title"
                  className="tc center "
                  aria-describedby="simple-modal-description"
                >
                  <div className='modal' style={{position:'relative',width:'100%',height:'100%'}}>
                    <div className=" " style={{position:'relative',width:'100%',height:'100%'}}  >
                      {
                        this.state.cropped ? <img src={this.state.croppedImageShow} /> : (
                          <Cropper
                            image={this.state.imageSrc}
                            crop={this.state.crop}
                            zoom={this.state.zoom}
                            aspect={this.state.aspect}
                            onCropChange={this.onCropChange}
                            onCropComplete={this.onCropComplete}
                            onZoomChange={this.onZoomChange}
                          />
                        )
                      }

                    </div>
                    <button type="button" onClick={() => this.props.selectProfilePic(this.state.croppedImage)} className="color-btn  center mt0 pt0 mb0"> Upload Cropped Area</button>
                    
                    <button type="button" onClick={() => this.setState({ tookInput: false, cropped: false })} className="profile-btn white center mt0 pt0 mb0">  Cancel</button>

                  </div>
                </Modal>
              </div>
            )
        }
      </div>
    );
  }
}

function readFile(file) {
  return new Promise((resolve) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => resolve(reader.result), false);
    reader.readAsDataURL(file);
  });
}

export default App;
